<template>
	<!-- 主体区域 -->
	<section id="app">
		<XiaHead @addEvent='addEvent'></XiaHead>
		<XiaMain @del="del" :todoList='todoList'></XiaMain>
		<XiaFoot @clear="clear" :todoList="todoList"></XiaFoot>
	</section>
</template>

<script>
import XiaHead from './components/XiaHead'
import XiaMain from './components/XiaMain'
import XiaFoot from './components/XiaFoot'
export default {
	components: {
		XiaHead,
		XiaMain,
		XiaFoot
	},
	data() {
		return {
			todoList: JSON.parse(localStorage.getItem('todoList')) || [
				{ id: 1, name: ' 默认值' },
				{ id: 2, name: ' 默认值' },
			]
		}
	},
	methods: {
		addEvent(value) {

			this.todoList.unshift({
				id: +new Date(),
				name: value
			})

		},
		del(value) {
			this.todoList = this.todoList.filter(item => item.id != value)
		},
		clear(){
			this.todoList = []
		}
	},
	watch:{
		todoList:{
			deep:true,
			handler(){
				localStorage.setItem('todoList',JSON.stringify(this.todoList))
			}
		}
	}
}
</script>

<style></style>