<template>
	<!-- 输入框 -->
	<header class="header">
		<h1>小夏记事本</h1>
		<input @keyup.enter='addName' v-model.trim='name' placeholder="请输入任务" class="new-todo" />
		<button @click='addName' class="add">添加任务</button>
	</header>
</template>

<script>
	export default {
		data() {
			return {
				name: ''
			}
		},
		methods: {
			addName() {
				if(this.name.trim() === ''){
					alert("请输入内容")
					return
				}
				this.$emit('addEvent',this.name)
				this.name = ''
			}
		},
	}
</script>

